@import url(https://fonts.googleapis.com/css?family=Unica+One|Roboto+Slab);
:root {
  --teal: #01a2a6;
  --red: #a70011;
  --white: hsl(181, 1%, 92%);
  --wrapper-margin: 6px;
  --header-font: "Unica One", sans-serif;
  --body-font: "Roboto Slab", serif;
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background-color: #01a2a6;
  background-color: var(--teal);
  background: -webkit-linear-gradient(left, #02a69b 0%, #0295a6 100%);
  background: linear-gradient(90deg, #02a69b 0%, #0295a6 100%);
  line-height: 1.5;
  color: #203f40;
}

.loading {
  height: 245px;
  width: 100%;
  position: relative;
  --loading-size: 5em;
}

.loading::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: var(--loading-size);
  width: var(--loading-size);
  border-radius: 100%;
  border: 0.2em solid rgba(0, 0, 0, .1);
}

.loading::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  height: var(--loading-size);
  width: var(--loading-size);
  border-radius: 100%;
  -webkit-animation: loading .75s linear infinite;
          animation: loading .75s linear infinite;
  border: 0.2em solid rgba(0, 0, 0, 1);
  border-color: var(--teal) transparent transparent;
}

@-webkit-keyframes loading {
  from {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
  }
  to {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
            transform: translate(-50%, -50%) rotate(360deg);
  }
}

@keyframes loading {
  from {
    -webkit-transform: translate(-50%, -50%) rotate(0deg);
            transform: translate(-50%, -50%) rotate(0deg);
  }
  to {
    -webkit-transform: translate(-50%, -50%) rotate(360deg);
            transform: translate(-50%, -50%) rotate(360deg);
  }
}

html,
body,
#root {
  height: 100%;
}

.App {
  text-align: center;
  height: auto;
  display: flex;
  flex-direction: column;
}

.App-header {
  background-color: #eee;
  padding: 10px;
  /* height: 150px; */
  /* background-color: #222; */
  /* color: white; */
}

.App-title {
  font-size: 1.5em;
}

.Launch-link {
  color: #203f40;
  text-decoration: none;
  display: flex;
}

#elp-badge {
  margin: 0 3em;
  -webkit-filter: drop-shadow(0 4px 6px rgba(32, 63, 64, 0.2));
          filter: drop-shadow(0 4px 6px rgba(32, 63, 64, 0.2));
  will-change: filter, transform;
  -webkit-transition: all 150ms ease;
  transition: all 150ms ease;
}

